/*************** CUSTOM CHECKBOX***************/

.checkboxContainer {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.checkboxContainer #checkboxInput {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 21px;
  width: 21px;
  border: 1px solid #ec2434;
  border: 1px solid var(--base-danger, #ec2434);
}

.checkboxContainer:hover #checkboxInput ~ .checkmark {
  background-color: #370d17;
}

.checkboxContainer #checkboxInput:checked ~ .checkmark {
  background-color: #ec2434;
  background-color: var(--base-danger, #ec2434);
}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.checkboxContainer #checkboxInput:checked ~ .checkmark:after {
  display: block;
}

.checkboxContainer .checkmark:after {
  left: 7px;
  top: 3px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}

/*************** CUSTOM RADIO BUTTON ***************/

.radioContainer {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.radioContainer #radioInput {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.radiomark {
  position: absolute;
  top: 0;
  left: 0;
  height: 21px;
  width: 21px;
  border: 1.3px solid #ec2434;
  border: 1.3px solid var(--base-danger, #ec2434);
  border-radius: 50%;
}

.radioContainer:hover #radioInput ~ .radiomark {
  background-color: #370d17;
}

.radioContainer #radioInput:checked ~ .radiomark {
  background-color: transparent;
}

.radiomark:after {
  content: "";
  position: absolute;
  display: none;
}

.radioContainer #radioInput:checked ~ .radiomark:after {
  display: block;
}

.radioContainer .radiomark:after {
  top: 5px;
  left: 5px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #ec2434;
  background: var(--base-danger, #ec2434);
}

